<template>
  <div class="board-header">
    <div class="board-notif" v-if="showUp">
      <div class="board-notif-body" v-if="mission">
        <div class="board-notif-number">{{ missionId }}</div>
        <div class="board-notif-content">{{ mission }}</div>
      </div>
    </div>
    <div class="board-notif-badge" v-if="showUp"></div>
  </div>
</template>

<script>
import MISSIONS from "@/missions";

export default {
  data: function() {
    return {
      missionId: null,
      mission: null,
      showUp: false
    };
  },
  methods: {
    show: function(missionId) {
      this.showUp = true;

      setTimeout(() => {
        this.missionId = missionId;
        this.mission = MISSIONS[missionId];
      }, 350);
    },
    hide: function() {
      this.mission = null;
      this.missionId = null;
      this.showUp = false;
    }
  }
};
</script>