export const sleep = function (ms) {
  return new Promise(res => setTimeout(res, ms));
}

export const arrayContainsArray = function (checkArray, targetArray) {
  return checkArray.every(i => targetArray.includes(i));
}

export const compareArray = function (arr1, arr2) {
  return arr1.every(i => arr2.includes(i)) && arr1.length == arr2.length;
}

export const arrayCollectionContainsArray = function (collection, checkArray) {
  if (collection.length === 0) return false;

  let result = true;

  collection.forEach((array) => {
    if (!compareArray(array, checkArray)) {
      result = false;
    }
  })

  return result;
}