<template>
  <div class="bingo-item" :class="{ opened: opened, active: activated, chained: chained }">
    <div class="bingo-item-inner"></div>
  </div>
</template>

<script>
export default {
  props: {
    cardNumber: {
      type: String,
      required: true
    },
    opened: {
      type: Boolean,
      required: true
    }
  },
  data: function() {
    return {
      activated: false,
      chained: false,
    };
  },
  methods: {
    activate: function() {
      this.activated = true;
    },
    lightUp: function() {
      this.activated = true;
      this.chained = true;
    },
    lightDown: function() {
      this.chained = false;
    }
  }
};
</script>

