<template>
  <div class="board">
    <BingoHeader ref="bingoHeader" />

    <div class="board-game">
      <div class="bingo-list">
        <BingoCard
          v-for="(missionId, cardKey) in cards"
          :key="cardKey"
          :mission-id="missionId"
          :ref="'card_' + cardKey"
          :card-number="cardKey"
          :opened="completed.includes(parseInt(cardKey)) && !animation.includes(parseInt(cardKey))"
        />
      </div>
    </div>

    <BingoFlash ref="bingoFlash" v-on:finish-bingo-animation="activateAnimation()" />

    <BingoFooter />
  </div>
</template>

<script>
import {
  sleep,
  arrayContainsArray,
  arrayCollectionContainsArray
} from "@/functions";
import BINGO_PATTERNS from "@/bingoPatterns";

import BingoCard from "./BingoCard";
import BingoFlash from "./BingoFlash";
import BingoHeader from "./BingoHeader";
import BingoFooter from "./BingoFooter";

export default {
  components: {
    BingoCard,
    BingoFlash,
    BingoHeader,
    BingoFooter
  },
  props: {
    boardData: {
      type: Object,
      required: true
    },
    profile: {
      type: Object,
      required: true
    }
  },
  data: function() {
    return {
      cards: {},
      completed: [],
      animation: [],
      finishedNumbers: [],
      originialBingoCombos: [],
      bingoCombos: [],
      alreadyRanBingoCombos: []
    };
  },
  created: function() {
    this.fetchData();
  },
  mounted: function() {
    this.fetchData();
    window.onload = () => {
      setTimeout(async () => {
        console.log(this.boardData)
        await this.setup();
      }, 350)
    };
  },
  methods: {
    setup: async function() {
      await this.activateAnimation();
    },
    fetchData: async function() {
      const resultData = this.boardData.data;
      this.cards = resultData.card;
      this.completed = resultData.comp;

      this.animation = resultData.anime.sort(
        (a, b) => parseInt(a) - parseInt(b)
      );

      this.finishedNumbers = this.completed.filter(
        n => !this.animation.includes(n)
      );
      let finishedAnime = this.completed.filter(
        n => !resultData.anime.includes(n)
      );

      BINGO_PATTERNS.forEach(pattern => {
        if (arrayContainsArray(pattern, finishedAnime)) {
          this.originialBingoCombos.push(pattern);
        }
      });
    },
    activateAnimation: async function() {
      if (this.animation.length === 0) return;
      // https://vuejs.org/v2/api/#ref
      const animateTarget = this.animation[0];
      await this.activateACard(animateTarget);
      this.finishedNumbers.push(animateTarget);
      this.finishedNumbers = this.finishedNumbers.sort((a, b) => a - b);
      this.animation.splice(0, 1);
      await this.checkOnBingo();
    },
    activateACard: async function(target) {
      await sleep(500);
      this.$refs[`card_${target}`][0].activate();
      await sleep(200);
      this.$refs.bingoHeader.show(target);
      await sleep(1500);
      this.$refs.bingoHeader.hide();
      await sleep(500);
    },
    performBingoOnARow: async function(row) {
      for (let i = 0; i < row.length; i++) {
        let animateTarget = row[i];
        await this.lightningACard(this.$refs[`card_${animateTarget}`][0]);
      }
      // TODO: fix me:
      let animateIcon;
      if (arrayContainsArray(row, [1, 2, 3])) {
        animateIcon = "mickey";
      } else if (arrayContainsArray(row, [3, 6, 9])) {
        animateIcon = "donald";
      }
      await this.bringFlashOut(animateIcon);
    },
    lightningACard: async function(target) {
      await sleep(200);
      target.lightUp();
      await sleep(100);
      target.lightDown();
    },
    bringFlashOut: async function(animateIcon) {
      await sleep(500);
      this.$refs.bingoFlash.activate(animateIcon);
      await sleep(750);
      this.$refs.bingoFlash.activateSvg();
    },
    checkOnBingo: async function() {
      for (let i = 0; i < BINGO_PATTERNS.length; i++) {
        let pattern = BINGO_PATTERNS[i];

        if (
          arrayContainsArray(pattern, this.finishedNumbers) &&
          !arrayCollectionContainsArray(this.originialBingoCombos, pattern) &&
          !arrayCollectionContainsArray(this.alreadyRanBingoCombos, pattern)
        ) {
          await this.performBingoOnARow(pattern);
          this.alreadyRanBingoCombos.push(pattern);
        }
      }
    }
  }
};
</script>